* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.box-buttons {
  justify-content: center;
  justify-items: end;
  padding: 150px;
  display: flex;
}

.buttons-start-and-stop {
  cursor: pointer;
  background-color: #fff;
  border: 2px solid #cfc5c5;
  border-radius: 7px;
  margin-right: 20px;
  padding: 20px;
  font-size: 18px;
  font-weight: bold;
  display: inline-block;
}

.buttons-start-and-stop:hover {
  color: #fafafa;
  background-color: rgba(143, 0, 0, .337);
}

.start-button {
  color: #faf6f6;
  background-color: rgba(68, 0, 228, .475);
}

.stop-button {
  color: #faf6f6;
  background-color: rgba(255, 0, 0, .44);
}

.input-date {
  margin-right: 5px;
  padding: 7px;
  font-size: 18px;
}

.disable-timer {
  color: rgba(0, 0, 0, .5);
  background-color: #d4d1d1;
  border: 0;
  border-radius: 4px;
  padding: 9px;
  font-size: 18px;
}

.is-active-timer {
  cursor: pointer;
  padding: 7px;
  font-size: 18px;
}

.is-active-timer:hover {
  background-color: rgba(175, 131, 131, .467);
}

.timer {
  display: flex;
}

